</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 22,  //当前表格的名称编号
        setting: [],
        form: {
          'a2_12': '0.00',
          'a5_12': '0.00',
          'a6_12': '0.00',
          'a11_12': '0.00',
          'a14_12': '0.00',
          'a21_12': '0.00',
          'a23_12': '0.00',
        },
        com:[]
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name];
      }
    },
    mounted() {
      this.inputChangeEvent();
      this.inputClickEvent();
      this.getSetting();  //获取后台设置数据
      this.formrevise();  //获取表间数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //预设数据
        this.com = this.$store.state.companyCurrent.gscompany;
        //this.setting = {'1_1': 10, '2_2': 5}
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key]*1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        // this.fn2(2, 14, [1], [0.16, 0.16, 0.13, 0.10, 0.10, 0.06, 0, 0, 0.06, 0.05, 0.05, 0.04, 0.03, 0.03], [9, 12], 3);  //2=1*税率
        // this.fn2(4, 14, [3], [0.16, 0.16, 0.13, 0.10, 0.10, 0.06, 0, 0, 0.06, 0.05, 0.05, 0.04, 0.03, 0.03], [9, 12], 3);  //4=3*税率
        // this.fn2(6, 14, [5], [0.16, 0.16, 0.13, 0.10, 0.10, 0.06, 0, 0, 0.06, 0.05, 0.05, 0.04, 0.03, 0.03], [9, 12], 3);  //6=5*税率
        // this.fn2(8, 6, [7], [0.16, 0.16, 0.13, 0.10, 0.10, 0.06], [], 3);  //8=7*税率
        this.fn1(9, 23, [1, 3, 5, 7], [7, 8, 18, 19], 1);      //9=1+3+5
        this.fn1(10, 23, [2, 4, 6, 8], [7, 8, 18, 19], 1);    //10=2+4+6
        this.fn1(11, 23, [9, 10], [], 1);                       //11=9+10
        this.fn1(13, 23, [11, 12], [], 2);                       //13=11-12
        this.fn2(14, 17, [13], [0.16, 0.13, 0.13, 0.10, 0.09, 0.06, 0, 0, 0.06, 0.05, 0.05, 0.04, 0.03, 0.03, 0, 0, 0], [], 4);  //14=13/(1+税率)*税率
      },
      //(q  要修改的值列
      // r  要修改多少行
      // seed 每行参与计算的列
      // jump 需跳过执行的行
      // type 运算类型  {1加  2减  3乘  4除}
      // )
      fn1(q, r, seed, jump, type) {
        for (let i = 1; i <= r; i++) {
          if (type == 1) {
            var sum = 0;
            for (let j = 0; j < seed.length; j++) {
              let m = seed[j]
              let dom = J('input[name=' + i + '_' + m + ']')
              if(dom.val()){
                if (!isNaN(dom.val())) {
                  sum += parseFloat(dom.val())
                }
              }
            }
          } else if (type == 2) {
            var sum = 0;
            for (let j = 0; j < seed.length; j++) {
              let m = seed[j]
              let dom = J('input[name=' + i + '_' + m + ']')
              if(dom.val()){
                if (j == 0) {
                  sum = J('input[name=' + i + '_' + m + ']').val();
                  if (sum) {
                  } else {
                    sum = 0.00;
                    break;
                  }
                  continue;
                }
                if (!isNaN(dom.val())) {
                  sum -= parseFloat(dom.val())
                }
              }
            }
          }
          if (jump) {
            //判断是否是需要跳过运算的行
            if (J.inArray(i, jump) < 0) {
              J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
            }
          } else {
            J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
          }
        }
      },
      //综合(
      // q要修改的值列
      // r要修改多少行
      // seed每行参与计算的列
      // tax每行需要的税率
      // jump 需跳过执行的行
      // type 运算类型  {1加  2减  3乘  4除}
      // )
      fn2(q, r, seed, tax, jump, type) {
        for (let i = 1; i <= r; i++) {
          var sum = 0;
          if (type == 4) {
            for (let j = 0; j < seed.length; j++) {
              let m = seed[j]
              let dom = J('input[name=' + i + '_' + m + ']')
              if(dom.val()){
                if (!isNaN(dom.val())) {
                  sum = parseFloat(dom.val() / (1 + tax[i - 1]) * tax[i - 1])
                }
              }
            }
          } else if (type == 3) {
            for (let j = 0; j < seed.length; j++) {
              let m = seed[j]
              let dom = J('input[name=' + i + '_' + m + ']')
              if(dom.val()){
                if (!isNaN(dom.val())) {
                  sum = parseFloat(dom.val() * tax[i - 1])
                }
              }
            }
          }
          if (jump) {
            //判断是否是需要跳过运算的行
            if (J.inArray(i, jump) < 0) {
              J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
            }
          } else {
            J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
          }
        }
      },
      formrevise() {
        var name1 = ''
        if (this.$store.state.tableData[25]) {
          this.form.a2_12 = this.$store.state.tableData[25][4].value,
            this.form.a5_12 = this.$store.state.tableData[25][10].value,
            this.form.a6_12 = (parseInt(this.$store.state.tableData[25][16].value) + parseInt(this.$store.state.tableData[25][22].value)).toFixed(2),
            this.form.a11_12 = this.$store.state.tableData[25][28].value,
            this.form.a14_12 = this.$store.state.tableData[25][34].value,
            this.form.a21_12 = this.$store.state.tableData[25][40].value,
            this.form.a23_12 = this.$store.state.tableData[25][46].value
        } else {
          name1 = '《附列资料（三）》'
        }
        if(name1 != ''){
          this.eg('请按照填表顺序正确填写:'+name1+'表未填写！')
        }
      }
    }
  }
</script>

<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    width: 2000px;
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input, select {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    text-align: center;
    padding: 3px 2px;
  }

  .bg-write {
    background: #d6ebff;
  }

  .bg-unwrite {
    background: #ccccff;
  }

</style>



